<template>
    <div class="info-div" style="height:100%">
      <div class="info-header">
        <span class="callback" @click="resetComponent">异常事件</span>
        <span> > {{row.eventCategory}} ( {{row.accountNum}} 条)</span>
      </div>
      
       <div class="list-content detail-content " style="height:calc(100% - 2.5em);padding:0!important;margin-top:2.5em">
          <div class="tabs-show tab-div" style="margin:0.6em 0!important">
            <ul>
              <li v-for="(item, $index) in btns" :class="{'selectedLi': $index == tabIndex}"   @click="btnClick($index,item)" style="font-size: 0.94em;padding:0.6em 2em">
                 <span>{{item.name}}</span>
              </li>
            </ul>
          </div>
          <template v-for="(item, $index) in formList">
            <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options"  class="rightComponent" @getInput="getFrom"></inputs>     
          </template>
          <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 4em)', padding:0, width: '100%', 'font-size': '0.94em'}">
            <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding" v-if="tableShow" @rowDbClick="clickTableRow"></tables>
          </div>
    
      </div>
       <dialogs type="customer" ref="showExpection" v-if="showDialog" width="46em" overflow="auto">
         <div slot="header">
             {{operate === 0 ? '异常事件处理详情': '异常事件处理'}}
         </div>
        <div slot="content" >
          <ul class="dialog-ul" style="padding-bottom:0.6em">
            <li class="form-list" :class="item.classes" v-for="(item, $index) in dialogFrom" >
              <label  class="label-show" style="min-width:7em">
                <span class="emptyTips" v-if="!item.isEmpty && operate === 1 && item.isEdit"> * </span>
                {{item.label}} :&nbsp;
              </label>
              <label :title="item.value" class="label-show" style="text-align: left;margin-left:0;max-width: calc(100% - 8.5em);overflow: hidden;text-overflow: ellipsis; white-space: nowrap;" v-if="!item.isEdit">{{item.value}}</label>
            </li>
          </ul>
          <ul class="dialog-ul" style="border-top: 1px solid #4D5F75; padding-top: 1.3em">
            <li class="form-list" :class="item.classes" v-for="(item, $index) in editFrom" v-show="!item.unShow">
              <label  class="label-show" style="min-width:7em">
                <span class="emptyTips"  v-if="!item.isEmpty && operate === 1 && item.isEdit"> * </span>
                {{item.label}} :&nbsp;
              </label>
              <label :title="item.value" class="label-show" style="text-align: left;margin-left:0;max-width: calc(100% - 8.5em);overflow: hidden;text-overflow: ellipsis; white-space: nowrap;" v-if="!item.isEdit">{{item.value}}</label>
              <template v-if="item.isEdit"> 
                <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.option"  @getInput = "getFrom"></inputs>
                <combine   v-if="item.type == 'combineSelect'" @getSelects="getOperator" :option="item.option" width="calc(100% - 8.5em)"></combine>
              </template>
            </li>
          </ul>
        </div>
        <div slot="footer" v-if="operate === 1">
          <button class="button-default" @click="submitData">提交</button>
          <button class="button-default cancel-button" @click="cancelDialog">取消</button>
        </div>
      </dialogs>
      <dialogs type="alert" ref="alerts"></dialogs>
    </div>

</template>

<script>
   import combine from  '../../component/childComponent/CombineSelect.vue'
   export default {
     data() {
       return {
          tabIndex: 0,
          searchParam: {
            eventStatus: 1,
            eventCategory: '',
            startDate: '',
            endDate: '',
            cnoFlag: "0",
            deviceType:'',
            permissionId: ''
          },
          operate: 0, //0已处理信息展示，1：填写处理方案，更新数据库状态
          searchDate: '',
          tableHeight: '',
          tableShow: false,
          showDialog: true,
          TableOpt: {
              column: [
                {title: '异常状态', field: 'eventStatus', textAlign: 'center', width: '100',   type:"string", formatter: function (val) {
                  if(val == 0){
                     return '<span style="color:#D98F4A;text-decoration: underline;">未处理</span>';
                  }
                    return '<span style="color:#53AD82">已处理</span>';
                }},
                {title: '用户姓名', field: 'customerName', textAlign: 'center', width: '140'},
                {title: '表号', field: 'deviceNo', textAlign: 'center', width: '140'},
                {title: '安装地址', field: 'installAddr', textAlign: 'center', width: '180'},
                {
                  title: '异常类型',
                  field: 'eventCategory',
                  textAlign: 'center',
                  width: '140'
                },
                {
                  title: '发生时间',
                  field: 'alarmTime',
                  textAlign: 'center',
                  width: '140'
                },
                {
                  title: '异常描述',
                  field: 'eventContent',
                  textAlign: 'center',
                  width: '250'
                }
              ],
              url: 'comprehensive/queryExceptionList',
              method: 'post',
              // url: 'dist/json/exception.json',
              // method: 'get',
              pageSize: 20,
              pageNumber: 1,
              pageList: [20, 40, 60],
              queryParam: {cnoFlag : "0"}
            },
          currentRow: '',
          updateStatus: {
             solveContent : '',
             solveUserId: ''
          },
          btns: [
             {name: '已处理', eventStatus: 1},
             {name: '未处理', eventStatus: 0}
          ]  
       }
     },
     components: {
       combine
     },
     props: ['row', 'param', 'controls'],
     computed: {
     	formList: function () {
     	  let dates = this.initDate(true);
     	  return [
              {
                 type: 'input',
                 options: {
                            type: 'typeSearch',
                            key: 'searchParam',
                            value: '',
                            selectOpt: {
                                width: '7em',
                                cs: 'client',
                                data: [
                                    {text: '表号', value: 'deviceNo'},
                                    {text: '用户姓名', value: 'customerName'},
                                    {text: '安装地址', value: 'installAddr'}
                                ],
                                text: 'text',
                                value: 'value',
                                key: 'searchType',
                                constant: {value: 'deviceNo'}
                            }
                        }
               },
              {
                type: 'select',
                options: {
                  cs: 'client',
                  width: '7.5em',
                  data: [
                    {text: '全部状态', value: ''},
                    {text: '已完成', value: 1},
                    {text: '未完成', value: 0}
                  ],
                  key: 'searchParam.eventStatus'
                }
              }
                 
            ]
     	},
      dialogFrom: function () {
       let width = 'calc(100% - 7em)';
        return [
           {label: "用户姓名", value: this.currentRow.customerName, classes:'halfWidth'},
           {label: "表号", value: this.currentRow.deviceNo, classes:'halfWidth hasMarginLi'},
           {label: "发生时间", value: this.currentRow.alarmTime, classes:'halfWidth'},
           {label: "异常类型", value: this.currentRow.eventCategory, classes:'halfWidth hasMarginLi'},
           {label: "表计地址", value: this.currentRow.installAddr, classes:'allWidth'},
           {label: "异常描述", value: this.currentRow.eventContent, classes:'allWidth'}
        ]
      },
      editFrom: function () {
        let width = 'calc(100% - 8.5em)';
        return [
         {
            label: "处理人员", 
            classes:this.operate === 0 ? 'halfWidth' : 'allWidth',
            value: this.currentRow.alarmDealPerson, 
            isEmpty: false, 
            isEdit: this.operate === 1,
            type: 'combineSelect',
            option: {
                inputOpt: {
                       width: '100%',
                       type: 'typeSearch',
                       hideSelect: false,
                       selectOpt: {
                           width: '7.5em',
                           cs: 'client',
                           data: [
                               {text: '人员姓名', value: 'userName'}
                           ],
                           key: 'searchType'
                       }
                   },
                   autoSearch: true,
                   url: 'replenish/queryUser',
                   method: 'post',
                   paramType: 'form'
            }
           },

            {label: "处理时间",   classes:'halfWidth hasMarginLi', value: this.currentRow.alarmDealTime, unShow: this.operate === 1},
            {
            label: "处理方案", 
            classes:'allWidth',
            value: this.currentRow.alarmDealDescribe, 
            isEmpty: false, 
            isEdit: this.operate === 1,
            type: 'input',
            option: {
                 width: width,
                 type: 'textArea',
                 placeHolder: '不超过200个字符',
                 length: 200,
                 key: 'updateStatus.solveContent',
                 errorMsg: '处理方案：200个任意字符'
            }
           }
        ]
      }
     },
    created: function () {

       for(let item in this.param) {
          this.searchParam[item] = this.param[item];
       }
       this.searchParam['eventCategory'] = this.row.eventCategory;
        //console.log(this.searchParam);
    }, 
    mounted: function () {
     // console.log(this.searchParam);
      this.TableOpt.queryParam = this.getSearchData(this.searchParam);
      this.tableShow = true;
      let that = this;
      // window.onresize = function () {
      //   that.getHeight('calc(100% - 5em)');
      // }
    },
     methods: {
       windowChange: function () {
           this.getHeight('calc(100% - 5em)');
       },
       btnClick: function (index, row) {
          this.tabIndex = index;
          this.searchParam['eventStatus'] = row.eventStatus;
          this.getSearchTable();
       },
       resetComponent: function () {
            this.$emit('closeDiv');
          },
       cancelDialog: function () {
           this.$refs.showExpection.close();
           this.showDialog = false;
           this.currentRow = '';
       },
       getFrom: function (val) {
          if (val.combine) {
              if (this.searchParam[val.key.split('.')[1]] !== val.val) {
                  this.searchParam = this.cleanSearchData(this.searchParam, ['eventStatus', 'eventCategory', 'startDate', 'endDate', 'cnoFlag', 'permissionId','deviceType']);
                  this.getOption(val, true);
                  this.getSearchTable();
              }
          } else {
                this.getOption(val, true);
          }
       },
       submitData: function () {
         if(this.updateStatus.solveContent === '' || typeof this.updateStatus.solveContent === 'undefined') {
           this.$refs.alerts.show({msg: '异常处理方案不能为空！'})
         } else if(this.updateStatus.solveUserId === '' || typeof this.updateStatus.solveUserId === 'undefined') {
           this.$refs.alerts.show({msg: '异常处理人员不存在，请重新输入！'})
         } else {
           let that = this;
           let arr = that.updateStatus;
           arr.id = that.currentRow.id;
           that.getRequest({
              url: 'comprehensive/DeviceEventUpdate',
              method: 'post',
              param: arr,
              success: function(data) {
                that.$refs.alerts.show({msg: data.message});
                that.cancelDialog();
                that.getSearchTable();
              },
              error: function(data) {
                that.$refs.alerts.show({msg: data.message})
              }
           })
         }
       },
       getOperator: function (val) {
           if (val.userId !== null && val.userId !== '') {
              this.updateStatus['solveUserId'] = val.userId;
           }
           if(val === '' || val === 'error') {
              this.updateStatus['solveUserId'] = ''
           }
       },
       getSearchTable: function () {
           let arr = this.getSearchData(this.searchParam);
           this.searchTable(arr, 1);
        },
       clickTableRow: function (row) {
           let that = this;
           that.currentRow = row.val;
              that.operate = row.val.eventStatus == 0 ? 1: 0;
              if((that.controls[29] && row.val.eventStatus == 0) || row.val.eventStatus == 1) {
                  that.showDialog = true;
                  that.$nextTick(function () {
                     that.$refs.showExpection.show({
                        hasClose: true,
                        action: {
                          cancel: function () {
                            that.showDialog = false;
                            that.currentRow = ''
                          }
                        }
                    });
                 })
              } 
        } 
      },
      watch: {}
   }
</script>

<style lang="scss">
	.specailTimer{
		float:right!important;
		.date-button {
          line-height: 1.7
		}
	}
  .rightComponent{
    float:right!important;
    margin:0.6em 0 0.6em 0.6em;
    font-size:0.94em
  }
</style>


