<template>
  <div class="all-container" :class="{accountContainer : !showInfo}">
     <info :row="currentRow" @closeDiv="closeDiv" ref="infoDetail" :param = "searchParam" :controls="controls" v-if="showInfo"></info>
    <div class="info-div" v-show="!showInfo" style="height:calc(100% - 4em);margin-top:3.2em">
      <div class="info-header operate-div" style="right:1.2em;top:0em">
        <template v-for="(item, $index) in formList">
          <datePicker ref="timer"  v-if="item.type == 'timer'" type='doubleDate' class="specailTimer"
                      :dateOpt="item.options" @getDatePicker="getOption"></datePicker>  
           <selects ref="selects"   style="float:right;margin-right:1em" v-if="item.type == 'select'" :option="item.options"
                         @getSelect="getFrom"></selects>              
        </template>
      </div>
      <div class="list-content detail-content " style="height:100%;padding:0.6em 0 0 0!important;font-size:0.94em">
          <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% )', padding:'0 0', width: '100%'}">
            <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding" v-if="tableShow" @rowDbClick="clickTableRow"></tables>
          </div>
    
      </div>
    </div>
  </div> 
</template>

<script>
   import info from './ExceptionHandling.vue'
   export default {
     data() {
       return {
          controls: [],
          showInfo: false,
          searchParam: {
            startDate: '',
            endDate: '',
            permissionId: '',
            deviceType:'07'
          },
          searchDate: '',
          tableHeight: '',
          tableShow: false,
          TableOpt: {
              column: [
                {
                  title: '异常类型',
                  field: 'eventCategory',
                  textAlign: 'center',
                  width: '25%'
                },
                {
                  title: '异常告警数',
                  field: 'accountNum',
                  textAlign: 'center',
                  width: '25%'
                },
                {
                  title: '已处理数',
                  field: 'dealNum',
                  textAlign: 'center',
                  width: '25%'
                },
                 {
                  title: '未处理数',
                  field: 'undealNum',
                  textAlign: 'center',
                  width: '25%'
                }
              ],
              url: 'comprehensive/queryExcAccountList',
              method: 'post',
              // url: 'dist/json/exception.json',
              // method: 'get',
              pageSize: 9999,
              pageNumber: 1,
              queryType: 'form',
              // pageList: [20, 40, 60],
              queryParam: {},
              showTotal: true,
              isExport: true,
              exportURL: 'comprehensive/queryExcAccountExcel',
              noExportCurrent: true,
               dataRow: 'list',
               totalOpt: [
                  {title: '异常告警数', field: 'accountNum', textAlign: 'center', width: '25%'},
                  {title: '已处理数', field: 'dealNum', textAlign: 'center', width: '25%'},
                  {title: '未处理数', field: 'undealNum', textAlign: 'center', width: '25%'}]
            },
          currentRow: ''
       }
     },
     components: {
      info
     },
     computed: {
     	formList: function () {
     	  let dates = this.initDate(true);
        let that=this;
     	  return [
     	      {
              	type: 'timer',
              	options: {
              	  key: 'searchDate',
                  value: {startDate: dates.start, endDate: dates.end},
                  width: '23em'
              	} 
              },{
              type: 'select',
              options: {
                key: 'searchParam.deviceType',
                cs: 'client',
                width: '9em',
                value: 'value',
                data: that.getEnumItem('deviceType').obj
              }
           }
            ]
     	}
     },
     mounted: function () {
       this.isIn();
       this.getAction();
    },
     methods: {
       init: function () {
          let date = this.initDate(true);
          this.searchDate = date.start + '至' + date.end;
          this.TableOpt.queryParam['startDate'] = date.start;
          this.TableOpt.queryParam['endDate'] = date.end;
          let action ='';
          if (this.controls[27]) {
            action = '27';
            if (this.controls[28]) {
               action = action + ',' + '28'
            }
          } else {
             if (this.controls[28]) {
               action = '28'
             }
          }
          this.TableOpt.queryParam['permissionId'] = this.searchParam['permissionId'] = action;
          this.TableOpt.queryParam['deviceType']=this.searchParam['deviceType'];
          this.tableShow = true;
          let that = this;
          /*window.onresize = function () {
            that.getHeight('calc(100%)');
          }*/
       },
       windowChange: function () {
          this.getHeight('calc(100%)');
      },
       closeDiv: function () {
         this.showInfo = false;
         this.getSearchTable();
       },
       getFrom: function (val) {
          let key=val.key.split('.')[1];
          this.getOption(val, true);
          if(key=='deviceType')
          { 
              this.getSearchTable();
          }
       },
       getSearchTable: function () {
           let arr = this.getSearchData(this.searchParam);
           this.searchTable(arr, 1);
        },
       clickTableRow: function (row) {
        
          let that = this;
          let param=row.val;
          param["deviceType"]=that.searchParam["deviceType"];
          
           that.currentRow = row.val;
           that.showInfo = true
       } 
      },
      watch: {
      'searchDate'() {
        let arr = this.searchDate.split('至');
        this.searchParam.startDate = arr[0];
        this.searchParam.endDate = arr[1];
        this.getSearchTable();
      }
    }
   }
</script>

<style lang="scss">
	.specailTimer{
		float:right!important;
		.date-button {
          line-height: 1.7
		}
	}
  .accountContainer{
    margin-top:0;
    height:100%;
    font-size: 1.07em;
  }
</style>


